import { FC } from 'react';

import {
  VARIANT_BADGE,
  VARIANT_PLAIN,
  VARIANT_PRIDE,
  APPEARANCE_BADGE,
  APPEARANCE_PLAIN,
} from './Logo.constants';
import { StyledIcon } from './Logo.styles';
import { LogoProps } from './Logo.types';

import LogoStampSimpleBadge from '#icons/logo-stamp-simple-badge-32.svg';
import LogoStampSimplePlain from '#icons/logo-stamp-simple-plain-32.svg';
import LogoStampSimplePride from '#icons/logo-stamp-simple-pride-32.svg';

const Logo: FC<LogoProps> = ({ className, variant = VARIANT_BADGE }) => {
  const [svg, appearance] = {
    [VARIANT_PLAIN]: [LogoStampSimplePlain, APPEARANCE_PLAIN],
    [VARIANT_PRIDE]: [LogoStampSimplePride, APPEARANCE_BADGE],
  }[variant] || [LogoStampSimpleBadge, APPEARANCE_BADGE];

  return (
    <StyledIcon
      className={className}
      svg={svg}
      size="large"
      $appearance={appearance}
    />
  );
};

export default Logo;
