import { styled, css } from 'styled-components';

import Icon from '#components/Icon';

import { APPEARANCE_BADGE, APPEARANCE_PLAIN } from './Logo.constants';
import { WrapperProps } from './Logo.types';

/**
 * No background, just a coloured eagle
 */
const plain = css`
  color: ${({ theme }) => theme.color.primary.base};
`;

/**
 * Solid round background with a white eagle
 * @explain the climate logo is anomalous in that it has a gradient background
 * and one that does not contain values that exist int he palette. This is
 * currently under discussion. sufficed to say, this is a temporary solution.
 */
const climateGradient = 'linear-gradient(#47a4ed, #8ce9bc 72%, #8ce9bc)';
const badge = css`
  border-radius: 50%;
  background: ${({ theme }) =>
    theme.name === 'climate' ? climateGradient : theme.color.primary.base};
  color: ${({ theme }) => theme.color.ink.inverted};
`;

export const StyledIcon = styled(Icon)<WrapperProps>`
  display: inline-block;

  ${({ $appearance }) =>
    ({
      [APPEARANCE_BADGE]: badge,
      [APPEARANCE_PLAIN]: plain,
    })[$appearance]}
`;
